import React from 'react';
import Loadable from 'react-loadable'

import DefaultLayout from './containers/DefaultLayout';

function Loading() {
  return <div>Loading...</div>;
}

const UserDashboard=Loadable({
  loader: () => import('./views/UserDashboard/UserDashboard'),
  loading: Loading,
});

const Restaurant=Loadable({
    loader: () => import('./views/Restaurant/Restaurant'),
  loading: Loading,
  });

const UserFavorCategory=Loadable({
    loader: () => import('./views/UserFavorCategory/UserFavorCategory'),
  loading: Loading,
  });

const UserFavorIngredient=Loadable({
    loader: () => import('./views/UserFavorIngredient/UserFavorIngredient'),
  loading: Loading,
  });

const UserWishlist=Loadable({
    loader: () => import('./views/UserWishList/UserWishList'),
  loading: Loading,
  });

const UserReviews=Loadable({
    loader: () => import('./views/UserReviews/UserReviews'),
  loading: Loading,

});

const Friends=Loadable({
    loader: () => import('./views/Friends/Friends'),
  loading: Loading,

  });

const RestaurantManage=Loadable({
  loader: () => import('./views/RestaurantManage/RestaurantManage'),
  loading: Loading

  });

const SingleRestaurantManage=Loadable({
    loader: () => import('./views/RestaurantManage/SingleRestaurantManage'),
  loading: Loading

});

// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '/', exact: true, name: 'Home', component: DefaultLayout },
  { path: '/userDashboard', name: 'RestaurantManage', component: UserDashboard },
  { path: '/userFavorCategory', name: 'RestaurantManage', component: UserFavorCategory },
  { path: '/userFavorIngredient', name: 'RestaurantManage', component: UserFavorIngredient },
  { path: '/userWishlist', name: 'UserWishlist', component: UserWishlist },
  { path: '/userReviews', name: 'UserReviews', component: UserReviews },
  {path: '/friends', name: 'Friends', component: Friends},
  { path: '/restaurant', name: 'Restaurant', component: Restaurant },
  { path: '/restaurantManage', name: 'RestaurantManage', component: RestaurantManage },
  { path: '/singleRestaurantManage', name: 'SingleRestaurantManage', component: SingleRestaurantManage },

];

export default routes;
