import React, {Component} from 'react';
import {
	Button,
	Card,
	CardBody,
	CardFooter,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	Col,
	Container,
	Form,
	Input,
	InputGroup,
	InputGroupAddon,
	InputGroupText,
	Row
} from 'reactstrap';
import axios from 'axios';

class Register extends Component {
	constructor(props) {
		super(props);
		this.state = {
			username: "",
			password: "",
			error: false,
			error_msg: ""
		};
		this.createUser = this.createUser.bind(this);
		this.loginUser = this.loginUser.bind(this);
		this.handleChange = this.handleChange.bind(this);
	}

	createUser() {
		//post request
		//redirect to login
		console.log("valid new user with username:" + this.state.username + ", password:" + this.state.password);
		axios.post('http://localhost:8080/api/register', {
			username: this.state.username,
			password: this.state.password
		})
			.then(response => {
				if (response.data.status) {
					window.alert("create successfully, redirect to login page!");
					this.props.history.push('./login');
				} else {
					this.setState({
						error: true,
						error_msg: response.data.msg
					})
				}
			}, error => {
				console.error(error);
				this.setState({
					error: true,
					error_msg: error.msg
				})
			});
	}

	handleError = () => {
		this.setState({error: false})
	};

	loginUser() {
		this.props.history.push('./login');
	}

	handleChange = event => {
		this.setState({
			[event.target.name]: event.target.value
		});
	};

	render() {
		return (
			<div className="app flex-row align-items-center login-bg">
				<Container>
					<Row className="justify-content-center">
						<Col md="6">
							<Card className="mx-4">
								<CardBody className="p-4">
									<Form>
										<h1>Register</h1>
										<p className="text-muted">Create your account</p>
										<InputGroup className="mb-3">
											<InputGroupAddon addonType="prepend">
												<InputGroupText>
													<i className="icon-user"/>
												</InputGroupText>
											</InputGroupAddon>
											<Input type="text" name="username" value={this.state.username}
												   onChange={this.handleChange}/>
										</InputGroup>
										<InputGroup className="mb-3">
											<InputGroupAddon addonType="prepend">
												<InputGroupText>
													<i className="icon-lock"/>
												</InputGroupText>
											</InputGroupAddon>
											<Input type="password" name="password" value={this.state.password}
												   onChange={this.handleChange}/>
										</InputGroup>
										<Button color="success" block onClick={this.createUser}>Create Account</Button>
										<Button color="info" style={{color: "white"}} block
												onClick={this.loginUser}>Login</Button>
									</Form>
								</CardBody>
							</Card>
						</Col>
					</Row>
				</Container>
				<Modal isOpen={this.state.error} toggle={this.handleError} className="modal-danger">
					<ModalHeader>{this.state.error_msg}</ModalHeader>
					<ModalBody>
						Something went wrong :( Please check again.
					</ModalBody>
					<ModalFooter>
						<Button color="primary" onClick={this.handleError}>Got it</Button>
					</ModalFooter>
				</Modal>
			</div>
		);
	}
}

export default Register;
