import React, {Component} from 'react';
import {
	Badge,
	DropdownItem,
	DropdownMenu,
	DropdownToggle,
	Nav,
	NavDropdown,
	Dropdown,
	NavItem,
	NavLink
} from 'reactstrap';
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom';

import {AppAsideToggler, AppHeaderDropdown, AppNavbarBrand, AppSidebarToggler} from '@coreui/react';
import avatar from '../../assets/img/brand/yelp-logo.svg';

const propTypes = {
	children: PropTypes.node,
};

const defaultProps = {};

class DefaultHeader extends Component {
	constructor(props) {
		super(props);

		this.state = {
			username: sessionStorage.getItem("user_name"),
			dropdownOpen: false
		};
	}

	toggle = () => {
		this.setState({
			dropdownOpen: !this.state.dropdownOpen
		});
	};

	render() {
		// eslint-disable-next-line
		const {children, ...attributes} = this.props;

		return (
			<React.Fragment>
				<Nav className={`ml-auto navbar ${this.props.transparent ? "navbar-transparent" : ""}`} pills>
					<NavLink href="#/userdashboard">Restaurants</NavLink>
					<NavLink href="#/userreviews">Review</NavLink>
					<NavLink href="#/friends">Friends</NavLink>
					<Dropdown className="nav-link" toggle={this.toggle} isOpen={this.state.dropdownOpen}>
						<DropdownToggle style={{fontSize: "1em"}} nav caret>
							Favourites
						</DropdownToggle>
						<DropdownMenu>
							<DropdownItem>
								<Link to="/userfavorcategory">Category</Link>
							</DropdownItem>
							<DropdownItem>
								<Link to="/userfavoringredient">Ingredient</Link>
							</DropdownItem>
							<DropdownItem>
								<Link to="/userwishlist">Wishlist</Link>
							</DropdownItem>
						</DropdownMenu>
					</Dropdown>
					{this.state.username ? (
						<AppHeaderDropdown direction="down">
							<DropdownToggle nav>
								<b className={this.props.transparent ? "navbar-transparent" : ""}>{this.state.username}</b>
								<img src={avatar} className="img-avatar" alt="yummy-logo"/>
							</DropdownToggle>
							<DropdownMenu right style={{right: 'auto'}}>
								<DropdownItem>
									<Link to={`/login`}><i className="fa fa-lock"/> Logout</Link>
								</DropdownItem>
							</DropdownMenu>
						</AppHeaderDropdown>
					) : (
						<AppHeaderDropdown direction="down">
							<DropdownToggle nav>
								<Link to={`/login`}
									  style={{textDecoration: "none"}}
									  className={this.props.transparent ? "navbar-transparent" : ""}>Login</Link>
								<img src={avatar} className="img-avatar" alt="yummy-logo"/>
							</DropdownToggle>
						</AppHeaderDropdown>
					)}
				</Nav>
			</React.Fragment>
		);
	}
}

DefaultHeader.propTypes = propTypes;
DefaultHeader.defaultProps = defaultProps;

export default DefaultHeader;
