import React, {Component} from 'react';
import {Redirect, Route, Switch} from 'react-router-dom';
import {Container} from 'reactstrap';

import {
	AppAside,
	AppBreadcrumb,
	AppFooter,
	AppHeader,
	AppSidebar,
	AppSidebarFooter,
	AppSidebarForm,
	AppSidebarHeader,
	AppSidebarMinimizer,
	AppSidebarNav,
} from '@coreui/react';
// sidebar nav config
import navigation from '../../_usernav';
import navigation_admin from '../../_adminnav';
// routes config
import routes from '../../routes';
import DefaultFooter from './DefaultFooter';
import DefaultHeader from './DefaultHeader';

class DefaultLayout extends Component {
	constructor(props) {
		super(props);

		this.state = {
			username: sessionStorage.getItem("user_name"),
			navigation: sessionStorage.getItem("user_name") === "Admin" ? navigation_admin : navigation,
			dashboard: sessionStorage.getItem("user_name") === "Admin" ? "/restaurantManage" : "/userDashboard",
			navbarTransparent: false
		};
	}

	render() {
		return (
			<div className="app">
				<AppHeader>
					<DefaultHeader transparent={window.location.hash.toLowerCase() === "#/userdashboard"}/>
				</AppHeader>
				<div className="app-body">
					<main className="main">
						<Container fluid>
							<Switch>
								{
									routes.map((route, idx) => {
											if (route.component) {
												return <Route key={idx}
															  path={route.path}
															  exact={route.exact}
															  name={route.name}
															  render={props => <route.component {...props} />}/>;
											}
										},
									)}
								<Redirect from="/" to={this.state.dashboard}/>
							</Switch>
						</Container>
					</main>
				</div>
				<AppFooter>
					<DefaultFooter/>
				</AppFooter>
			</div>
		);
	}
}

export default DefaultLayout;
