import React, {Component} from 'react';
import {
	Button,
	Card,
	CardBody,
	Modal,
	ModalFooter,
	ModalBody,
	ModalHeader,
	CardGroup,
	Col,
	Container,
	Form,
	Input,
	InputGroup,
	InputGroupAddon,
	InputGroupText,
	Row
} from 'reactstrap';
import axios from 'axios';

class Login extends Component {
	constructor(props) {
		super(props);
		this.state = {
			username: "",
			password: "",
			error: false,
			error_msg: ""
		};
		this.loginUser = this.loginUser.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.register = this.register.bind(this);
	}

	loginUser() {
		//ajax request
		axios.post('http://localhost:8080/api/login', {
			username: this.state.username,
			password: this.state.password
		}).then(response => {
			if (response.data.status) {
				sessionStorage.setItem("user_id", response.data.uid);
				sessionStorage.setItem("user_name", this.state.username);
				if (this.state.username === "Admin") this.props.history.push('./restaurantManage');
				else this.props.history.push('./userDashboard');
			} else {
				this.setState({
					error: true,
					error_msg: response.data.msg
				})
			}
		}, error => {
			console.error(error);
			this.setState({
				error: true,
				error_msg: error.msg
			})
		});
	}

	handleError = () => {
		this.setState({error: false})
	};

	handleChange = event => {
		this.setState({
			[event.target.name]: event.target.value
		});
	};

	register() {
		this.props.history.push('./register');
	}

	render() {
		return (
			<div className="app flex-row align-items-center login-bg">
				<Container>
					<Row className="justify-content-center">
						<Col md="8">
							<CardGroup>
								<Card className="p-4">
									<CardBody>
										<Form>
											<h1>Login</h1>
											<p className="text-muted">Sign In to your account</p>
											<InputGroup className="mb-3">
												<InputGroupAddon addonType="prepend">
													<InputGroupText>
														<i className="icon-user"/>
													</InputGroupText>
												</InputGroupAddon>
												<Input type="text" name="username" placeholder="Username"
													   onChange={this.handleChange}/>
											</InputGroup>
											<InputGroup className="mb-4">
												<InputGroupAddon addonType="prepend">
													<InputGroupText>
														<i className="icon-lock"/>
													</InputGroupText>
												</InputGroupAddon>
												<Input type="password" name="password" placeholder="Password"
													   autoComplete="current-password" onChange={this.handleChange}/>
											</InputGroup>
											<Row>
												<Col xs="6">
													<Button color="primary" className="px-4"
															onClick={this.loginUser}>Login</Button>
												</Col>
											</Row>
										</Form>
									</CardBody>
								</Card>
								<Card className="text-white bg-primary py-5 d-md-down-none" style={{width: 44 + '%'}}>
									<CardBody className="text-center">
										<div>
											<h2>Sign up</h2>
											<p>Sign up for your favourite restaurants. They're all in <b>Yummy
												Restaurants!</b></p>
											<Button color="primary" className="mt-3" active onClick={this.register}>Register
												Now!</Button>
										</div>
									</CardBody>
								</Card>
							</CardGroup>
						</Col>
					</Row>
				</Container>
				<Modal isOpen={this.state.error} toggle={this.handleError} className="modal-danger">
					<ModalHeader>{this.state.error_msg}</ModalHeader>
					<ModalBody>
						Something went wrong :( Please check again.
					</ModalBody>
					<ModalFooter>
						<Button color="primary" onClick={this.handleError}>Got it</Button>
					</ModalFooter>
				</Modal>
			</div>
		);
	}
}

export default Login;
